<template>
  <div id="app">
    <div v-if="!link" class="note">
      在 {{ localUrl }} 後輸入任意字串，產生 link：
    </div>
    <div v-if="!link" class="note-link">demo://test-link/${input_str}</div>
    <div class="note-link">{{ link }}</div>

    <a href="https://weihsun1990.github.io/#/chart" target="_blank">chart</a>
    <br />
    <a href="https://weihsun1990.github.io/#/pie-chart" target="_blank"
      >chart</a
    >
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      link: "",
      localUrl: "",
    };
  },
  created() {
    const url = new URL(window.location.href);
    this.localUrl = `${url.protocol}//${url.host}/#/`;
    const link = `${window.location.href}`.replace(this.localUrl, "");
    if (link) {
      this.link = `demo://test-link/${link}`;
      this.$toAppLink(link);
    }
  },
  mounted() {},
};
</script>

