import Vue from "vue";
import App from "./App.vue";

import VueRouter from "vue-router";

Vue.config.productionTip = false;

Vue.prototype.$toAppLink = function (param) {
  window.onload = function () {
    let redirectUrl = "";

    const userAgent = navigator.userAgent.toLowerCase();
    if (/iphone|ipad|ipod/.test(userAgent)) {
      // iOS
      redirectUrl = "https://apps.apple.com/tw/app/國泰人壽/id432046643";
    } else if (/android/.test(userAgent)) {
      // Android
      redirectUrl =
        "https://play.google.com/store/apps/details?id=com.cathay.main&hl=zh_TW&gl=US";
    } else {
      // PC/NB
      // redirectUrl =
      //   "https://online.cathaylife.com.tw/OIWeb/servlet/HttpDispatcher/OIA0_0100/prompt";
    }

    window.location = `demo://test-link/${param}`;

    if (redirectUrl) {
      setTimeout(function () {
        window.location = redirectUrl;
      }, 500);
    }
  };
};

Vue.use(VueRouter);
const routes = [{ path: "/" }];

const router = new VueRouter({
  mode: "hash",
  routes: routes,
});

new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
